.button {
    background-color: #f9d342;
    color: #1e1e2f;
    border: none;
    padding: 10px 15px;
    font-size: 1rem;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s, transform 0.2s;
  }
  
  .button:hover {
    background-color: #e5be2c;
    transform: scale(1.05);
  }
  
  .button:disabled {
    background-color: #ccc;
    cursor: not-allowed;
  }
  