/* تصنيف الهيكل العام */
.product-grid {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
  }
  
  .product-card {
    width: 350px; /* تحديد عرض كل بطاقة */
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 8px;
    text-align: center;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }
  
  .search-container {
    display: flex;
    justify-content: center; /* وضع مربع البحث في وسط الصفحة */
    margin: 20px 0;
  }
  
  .search-container input {
    width: 300px; /* تحديد عرض مربع البحث */
    padding: 10px;
    border-radius: 5px;
    border: 1px solid #ccc;
    font-size: 16px;
  }
  
  h1, h2 {
    text-align: center;
  }
  